/* Resume.css */
body {
  font-size: 16px; /* Slightly smaller text */
  line-height: 1.5;
  font-family: 'Arial', sans-serif;
}

.resume {
  max-width: 1400px;
  width: 95%;
  margin: 20px auto;
  padding: 40px;
  background: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}

.resume__header {
  text-align: center;
  margin-bottom: 40px;
}

.resume__profile {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.resume__profile-picture {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.resume__header h1 {
  margin: 0;
  font-size: 2.8em; /* Slightly smaller than before */
  color: #2c3e50;
}

.resume__tagline {
  font-size: 1.4em; /* Slightly smaller */
  color: #34495e;
}

.resume__contact p {
  margin: 10px 0;
}

.resume__contact a {
  color: #2980b9;
  text-decoration: none;
}

.resume__contact a:hover {
  text-decoration: underline;
}

.resume__download {
  text-align: center;
  margin-bottom: 40px;
}

.resume__download a {
  display: inline-block;
  background: #2980b9;
  color: #fff;
  padding: 10px 20px;
  border-radius: 25px;
  text-decoration: none;
  font-size: 1.1em;
  font-weight: bold;
  transition: background 0.3s;
}

.resume__download a:hover {
  background: #1f648b;
}

.resume__section {
  margin-bottom: 50px;
}

.resume__section-title {
  font-size: 1.8em;
  font-weight: bold; /* Bold titles */
  color: #2c3e50;
  margin-bottom: 25px;
  border-bottom: 2px solid #ddd;
  padding-bottom: 15px;
}

.resume__list {
  list-style-type: none;
  padding: 0;
}

.resume__item {
  margin-bottom: 20px;
  padding: 15px;
  border-left: 5px solid #2980b9;
  color: #555;
  background: #ecf0f1;
  border-radius: 10px;
  transition: background 0.3s;
  cursor: pointer;
}

.resume__item:hover {
  background: #d0e6f7;
}

.resume__item-header {
  font-size: 1.2em; /* Slightly larger */
  font-weight: bold; /* Bold text */
  color: #2c3e50;
}

.resume__tags {
  margin-top: 10px;
}

.resume__tag {
  display: inline-block;
  background: #2980b9;
  color: #fff;
  padding: 5px 10px;
  border-radius: 20px;
  margin-right: 10px;
  margin-bottom: 5px;
  font-size: 0.9em;
}

.resume__item-details {
  margin-top: 15px;
  font-size: 0.9em; /* Slightly smaller details */
  color: #34495e;
}

.resume__skills {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.resume__skill {
  background: #2980b9;
  color: #fff;
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 0.9em;
}

.resume__item-bubble {
  margin-bottom: 20px;
  padding: 15px;
  border-left: 5px solid #2980b9;
  color: #555;
  background: #ecf0f1;
  border-radius: 10px;
  transition: background 0.3s;
  cursor: pointer;
}

.resume__item-bubble:hover {
  background: #d0e6f7;
}

.resume__education-item {
  padding: 15px;
  border-left: 5px solid #2980b9;
  color: #555;
  background: #ecf0f1;
  border-radius: 10px;
  transition: background 0.3s;
  cursor: default;
}

.resume__education-item:hover {
  background: #d0e6f7;
}

@media (max-width: 1200px) {
  .resume {
    max-width: 95%;
    padding: 20px;
  }
}

@media (max-width: 900px) {
  .resume {
    padding: 10px;
  }

  .resume__header h1 {
    font-size: 2.2em;
  }

  .resume__tagline {
    font-size: 1.2em;
  }

  .resume__section-title {
    font-size: 1.4em;
  }
}

.resume__profile-pic {
  width: 450px; /* Adjust width as needed */
  height: 450px; /* Adjust height as needed */
  border-radius: 50%; /* Makes the image circular */
  object-fit: cover; /* Ensures the image covers the area without distortion */
  margin: 0 auto; /* Centers the image */
  display: block; /* Ensures the image is centered */
}
